import "./Intro.css";


function Titel(prop){
    return(
        <div className="titel-container" id={prop.id}>
            <video src='videos/Atano2.mp4' className="video" autoPlay loop muted alt= "Atano Video" />
            {/* <div className="atano-name-container">
                <img src="images/AtanoName01.png"  className="atano-name" alt= "Atano Name"/>
            </div> */}
            <img src="images/atanofoto01.png"  className="titel-bordspel" alt= "Atano Bordspel"/>
            <div className="titel-text-container">
                <div className="titel-text">
                Atano is een bordspel waarin de wereld Atano tot leven komt. Ieder speel ronde is 1 dag in het dorp Atano. 
                </div>
            </div>
        </div>
            
    )
}

export default Titel;